@charset "UTF-8";

@import "base";

//main
.main-wrap .kind::before{content: ''; display: inline-block; margin-right: 6px; width: 24px; height: 24px; background-repeat: no-repeat; background-size: 100% auto; background-position: center;}
.main-wrap .kind.fitness::before{background-image: url("../img/common/ico_sort_fitness.svg");}
.main-wrap .kind.class::before{background-image: url("../img/common/ico_sort_class.svg");}
.main-wrap .kind.employ::before{background-image: url("../img/common/ico_sort_employ.svg");}
.main-wrap .kind.insight::before{background-image: url("../img/common/ico_sort_insight.svg");}

.main-banner .swiper{overflow: hidden; position: relative; margin: 40px 0 60px; padding: 0 15.6% 34px; width: 100%;}
.main-banner .swiper .swiper-slide{overflow: hidden; position: relative; margin-right: 24px; width: 312px; height: 480px; border-radius: 24px;}
.main-banner .swiper .swiper-slide:last-of-type{margin-right: 0;}

.main-banner .swiper .swiper-slide a{display: block; padding: 48px 32px; width: 100%; height: 100%;}
.main-banner .swiper .swiper-slide .txt-box{position: relative; z-index: 2;}
.main-banner .swiper .swiper-slide .kind{@include flex(); margin-bottom: 20px; padding: 0 16px 0 12px; width: fit-content; height: 40px; font-size: 16px; font-weight: 500; color: #234281; border-radius: 20px; background-color: $c-white;}
.main-banner .swiper .swiper-slide .tit{overflow: hidden; text-overflow: ellipsis; display: -webkit-box; -webkit-line-clamp: 3; -webkit-box-orient: vertical; max-height: 90px; font-size: 24px; font-weight: 500; line-height: 1.25; color: $c-black; }
.main-banner .swiper .swiper-slide:hover .img-box{width: 120%; height: 120%;}
.main-banner .swiper .swiper-slide .img-box{position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 100%; height: 100%; transform-origin: 0 0; transition: width 0.4s, height 0.4s;}
.main-banner .swiper .swiper-slide .img-box img{height: 100%; object-fit: cover;}

.main-banner .swiper .swiper-slide.large{width: 872px;}
.main-banner .swiper .swiper-slide.large .tit{width: 58%; max-height: 150px; font-size: 40px; font-weight: 700;}
.main-banner .swiper .swiper-slide.large .desc{width: 58%; margin-top: 12px; font-size: 20px; line-height: 1.4;}

.main-banner .swiper .swiper-scrollbar{left: 50%; bottom: 0; transform: translateX(-50%); width: 648px; height: 2px; background-color: #D8DDE2; border-radius: 30px; transition: height 0.2s; z-index: 5;}
.main-banner .swiper .swiper-scrollbar:hover{height: 6px;}
.main-banner .swiper .swiper-scrollbar-drag{background-color: #2A4474;}

.main-white-box{padding: 48px 88px; background-color: $c-white; border-radius: 24px;}
.main-white-box + .main-white-box{margin-top: 32px;}
.main-white-box > .txt-box{margin-bottom: 32px;}
.main-white-box > .txt-box > .kind{@include flex(); margin-bottom: 20px; width: fit-content; font-size: 20px; font-weight: 700; color: $c-blue;}
.main-white-box > .txt-box > .kind::before{margin-right: 8px;}
.main-white-box > .txt-box > .tit{font-size: 32px; font-weight: 500; line-height: 1.19;}
.main-white-box > .txt-box > .desc{margin-top: 12px; font-size: 16px; color: #7C828B; line-height: 1.25;}
.main-white-box > .txt-box > .btn-group{justify-content: flex-start;}
.main-white-box > .btn-group{padding: 32px 0 0;}

.main-white-box.col{display: flex; }
.main-white-box.col > .txt-box{margin-bottom: 0;}

.main-contents .thumb-list .swiper-wrapper{display: grid; grid-template-columns: 41.2% 1fr 1fr; gap: 32px 24px;}

.main-contents .thumb-list .swiper-slide.large{grid-row: 1/3;}
.main-contents .thumb-list .swiper-slide.large .img-box{border-width: 0;}
.main-contents .thumb-list .swiper-slide.large .img-box::before{padding-top: 110%;}
.main-contents .thumb-list .swiper-slide.large .tit{ max-height: 72px; font-size: 24px;}

.main-contents .thumb-list .swiper-slide{transform: translateY(0); transition: transform 0.3s;}
//.main-contents .thumb-list .swiper-slide:hover{transform: translateY(-10px);}
.main-contents .thumb-list .swiper-slide a{display: block;}
//.main-contents .thumb-list .swiper-slide .img-box{overflow: hidden; position: relative; height: 192px; border-radius: 16px; border: 1px solid #D8DDE2;}
.main-contents .thumb-list .swiper-slide .img-box{overflow: hidden; position: relative; border-radius: 16px; border: 1px solid #D8DDE2;}
.main-contents .thumb-list .swiper-slide .img-box::before{content: ''; display: block; padding-top: 61.5%;}
.main-contents .thumb-list .swiper-slide .img-box img{position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); height: 100%; object-fit: cover;}
.main-contents .thumb-list .swiper-slide .sort{@include flex(); margin: 12px 0; font-size: 16px;}
.main-contents .thumb-list .swiper-slide .sort strong{@include flex(); font-weight: 500; color: $c-black;}
.main-contents .thumb-list .swiper-slide .sort strong::after{content: ''; display: inline-block; margin: 0 12px; width: 1px; height: 12px; background-color: #D8DDE2;}
.main-contents .thumb-list .swiper-slide .sort strong .icon{margin-right: 6px; width: 20px; height: 20px; background-color: transparent; background-repeat: no-repeat; background-position: center; background-size: 100% auto;}
.main-contents .thumb-list .swiper-slide .sort span{color: #7C828B;}
.main-contents .thumb-list .swiper-slide .tit{overflow: hidden;text-overflow: ellipsis; display: -webkit-box; max-height: 60px; -webkit-line-clamp: 2; -webkit-box-orient: vertical; font-size: 20px; font-weight: 500; line-height: 1.5; color: $c-black; text-align: left;}
.main-contents .thumb-list .swiper-slide .txt{display: flex; justify-content: space-between; align-items: center; margin-top: 13px; padding-top: 10px; font-size: 16px; border-top: 1px solid rgba(255, 255, 255, 0.1);}
.main-contents .thumb-list .swiper-slide .txt .status{font-weight: 700; color: #3575f6;}
.main-contents .thumb-list .swiper-slide .txt .period{color: #b9c0c6;}

.main-class{}
.main-class .thumb-list{overflow: hidden;}
.main-class .thumb-list .swiper-slide{flex: 1; margin-right: 24px; min-width: 268px;}
.main-class .thumb-list .swiper-slide:last-of-type{margin-right: 0;}
.main-class .thumb-list .img-box{overflow: hidden; position: relative; border-radius: 16px; border: 1px solid #D8DDE2;}
.main-class .thumb-list .img-box::before{content: ''; display: block; padding-top: 61.6%;}
.main-class .thumb-list .img-box img{position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); height: 100%; }
.main-class .thumb-list .txt-box .sort{margin: 16px 0 12px; font-size: 16px;}
.main-class .thumb-list .txt-box .sort strong{font-weight: 500; color: $c-blue;}
.main-class .thumb-list .txt-box .sort strong::after{content: ''; display: inline-block; margin: 0 12px; width: 1px; height: 12px; background-color: #D8DDE2;}
.main-class .thumb-list .txt-box .sort span{color: #7C828B;}
.main-class .thumb-list .txt-box .tit{overflow: hidden; text-overflow: ellipsis; display: -webkit-box; max-height: 60px; -webkit-line-clamp: 2; -webkit-box-orient: vertical; font-size: 20px; font-weight: 500; line-height: 1.5; color: $c-black; text-align: left;}
.main-class .thumb-list .txt-box .hash-tag{overflow: hidden; margin-top: 6px;  text-overflow: ellipsis; display: -webkit-box; -webkit-line-clamp: 1; -webkit-box-orient: vertical; white-space: nowrap;}
//.main-class .thumb-list .txt-box .hash-tag{ overflow: hidden; margin-top: 6px; text-overflow: ellipsis; white-space: nowrap;}
.main-class .thumb-list .txt-box .hash-tag span{margin-right: 10px; font-size: 16px; color: #7C828B; }
.main-class .thumb-list .txt-box .hash-tag span:last-child{margin-right: 0;}

.main-employ{}
.main-employ > .txt-box{flex-shrink: 0; width: 29.75%;}
.main-employ .thumb-list{overflow: hidden; margin-left: auto;}
.main-employ .thumb-list .swiper-slide{display: grid; grid-template-columns: 1fr 1fr; gap: 20px 24px;}
.main-employ .thumb-list .item{position: relative; border: 1px solid #D8DDE2; border-radius: 16px;}
.main-employ .thumb-list .item a{display: block; padding: 24px 20px 16px;}
.main-employ .thumb-list .item .badge{position: absolute; top: 25px; right: 24px;}
.main-employ .thumb-list .item .img-logo{overflow: hidden; width: 48px; height: 48px; border-radius: 8px; border: 1px solid #D8DDE2; background-color: #F4F5F8;}
.main-employ .thumb-list .item .txt-box{}
.main-employ .thumb-list .item .txt-box .sort{@include flex(); margin: 12px 0 8px; font-size: 16px;}
.main-employ .thumb-list .item .txt-box .sort strong{font-weight: 500; color: $c-blue;}
.main-employ .thumb-list .item .txt-box .sort strong::after{content: ''; display: inline-block; margin: 0 12px; width: 1px; height: 12px; background-color: #D8DDE2;}
.main-employ .thumb-list .item .txt-box .sort span{color: #2E3337;}
.main-employ .thumb-list .item .txt-box .tit{overflow: hidden; text-overflow: ellipsis; font-size: 20px; font-weight: 500; color: $c-black; display: -webkit-box; -webkit-line-clamp: 1; -webkit-box-orient: vertical;}
//.main-employ .thumb-list .item .txt-box .tit{overflow: hidden; text-overflow: ellipsis; font-size: 20px; font-weight: 500; color: $c-black; white-space: nowrap;}
.main-employ .thumb-list .item .txt-box .desc{margin-top: 20px; padding-top: 12px; font-size: 14px; color: #7C828B; border-top: 1px solid #D8DDE2;}
.main-employ .thumb-list .item .txt-box .desc span::after{content: ''; display: inline-block; margin: 0 8px; width: 1px; height: 12px; background-color: #D8DDE2;}
.main-employ .thumb-list .item .txt-box .desc span:last-child::after{display: none;}

.main-employ .thumb-list .swiper-button{@include flex('flex-end'); margin-bottom: 16px;}
.main-employ .thumb-list .swiper-button > div{margin-top: 0; width: 36px; height: 36px;}
.main-employ .thumb-list .swiper-button .swiper-button-prev{position: initial; margin-right: 12px; @include bg('common/ico_circle_left.svg');}
.main-employ .thumb-list .swiper-button .swiper-button-next{position: initial; @include bg('common/ico_circle_right.svg');}

.main-employ .btn-group.pc_only{display: flex;}
.main-employ .btn-group.mo_only{display: none;}

.main-partner{padding-top: 60px;}
.main-partner .banner{position: relative; overflow: hidden; margin-bottom: 24px; padding: 60px 86px 56px; border-radius: 24px;}
.main-partner .banner .txt-box{position: relative; z-index: 2;}
.main-partner .banner .txt-box .tit{margin-bottom: 24px; font-size: 24px; font-weight: 700;}
.main-partner .banner .img-box{position: absolute; top: 50%; transform: translateY(-50%);}

.main-partner .banner.employ-service{background-color: #DFE6F2;}
.main-partner .banner.employ-service .img-box{width: 290px; right: 16%;}

.main-partner .banner.info-service{position: relative; background-color: #15386F;}
.main-partner .banner.info-service::after{content: ''; display: block; position: absolute; top: 50%; right: 0; transform: translateY(-50%); width: 650px; height: 540px; @include bg('content/main_partner_obj_circle.png'); z-index: 1;}
.main-partner .banner.info-service .img-box{overflow: hidden; width: 171px; right: 22%; box-shadow: 0 20px 20px rgba(0, 0, 0, 0.16); border-radius: 42px; z-index: 2;}

.main-partner .partner-logo{display: grid; grid-template-columns: repeat(5, 15%); gap: 16px; padding: 74px 98px; justify-content: space-between;  background-color: #fff; border-radius: 24px;}
.main-partner .partner-logo li{overflow: hidden; position: relative; width: 100%; max-width: 156px;}
.main-partner .partner-logo li::before{content: ''; display: block; padding-top: 42%;}
.main-partner .partner-logo li img{position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); filter: grayscale(100%);}

@media all and (max-width:1919px) {
  .main-banner{margin: 0 auto; padding: 0 48px; max-width: 1416px;}
  .main-banner .swiper{padding-left: 0; padding-right: 0;}
}


@media all and (max-width:1200px) {
  .main-banner{padding: 0;}
  .main-banner .swiper{padding-left: 40px; padding-right: 40px;}

  .main-white-box{padding: 48px;}
  .main-white-box .thumb-list{overflow: hidden; margin-left: -48px; margin-right: -48px; padding: 0 48px;}

  .main-contents .thumb-list .swiper-wrapper{display: flex; gap: 0;}
  .main-contents .thumb-list .swiper-slide.large .img-box{border-width: 1px;}
  .main-contents .thumb-list .swiper-slide.large .img-box::before{padding-top: 61.5%;}
  .main-contents .thumb-list .swiper-slide.large .tit{ max-height: 60px; font-size: 20px;}

  .main-contents .thumb-list .swiper-slide{margin-right: 32px; width: 246px;}
  .main-contents .thumb-list .swiper-slide:last-child{margin-right: 0;}

  .main-employ{flex-wrap: wrap;}
  .main-employ > .txt-box{width: 100%;}

  .main-employ .thumb-list .pc_only{display: none;}
  .main-employ .thumb-list .mo_only{display: block;}
  .main-employ .thumb-list .swiper-slide{display: block; margin-right: 16px; min-width: 204px; width: fit-content;}
  .main-employ .thumb-list .swiper-slide:last-child{margin-right: 0;}

  .main-employ .thumb-list .item .txt-box .tit{display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical;}
}

@media all and (max-width:960px) {
  .main-banner .swiper .swiper-slide a{padding: 30px;}
  .main-banner .swiper .swiper-slide:hover .img-box{width: 100%; height: 100%;}

  .main-banner .swiper .swiper-slide.large{width: 312px;}
  .main-banner .swiper .swiper-slide.large .tit{width: 100%; max-height: 90px; font-size: 24px; font-weight: 500;}
  .main-banner .swiper .swiper-slide.large .desc{width: 100%; margin-top: 12px; font-size: 16px;}
  .main-banner .swiper .swiper-slide.large .img-box .pc_only{display: none;}
  .main-banner .swiper .swiper-slide.large .img-box .mo_only{display: block;}

  .main-box-list{margin-left: -48px; margin-right: -48px; padding: 40px 48px; background-color: $c-white;}
  .main-white-box.col > .txt-box{margin-bottom: 24px;}

  .main-white-box{padding: 0; border-radius: 0;}
  .main-white-box + .main-white-box{margin-top: 60px;}

  .main-employ .btn-group.pc_only{display: none;}
  .main-employ .btn-group.mo_only{display: flex; width: 100%;}

  .main-partner .banner{padding: 60px 40px;}
  .main-partner .banner.employ-service .img-box{right: 40px;}
  .main-partner .banner.info-service .img-box{right: 85px;}
  .main-partner .banner.info-service::after{right: -120px;}

  .main-partner .partner-logo{ grid-template-columns: repeat(3, 156px); padding: 74px 40px;}
}

@media all and (max-width: 720px) {
  .main-wrap .kind::before{margin-right: 1.67vw; width: 4.44vw; height: 4.44vw;}

  .main-banner .swiper{margin: 5.56vw 0 8.89vw; padding: 0 5.56vw 4.72vw;}
  .main-banner .swiper .swiper-slide{margin-right: 5.56vw; width: 81.11vw; height: 125.00vw; border-radius: 5.56vw;}
  .main-banner .swiper .swiper-slide a{padding: 8.89vw 6.67vw;}
  .main-banner .swiper .swiper-slide .kind{margin-bottom: 4.44vw; padding: 0 3.33vw 0 2.22vw; width: fit-content; height: 7.50vw; font-size: 3.33vw; border-radius: 5.56vw;}
  .main-banner .swiper .swiper-slide .tit{max-height: 20.83vw; font-size: 5.56vw; font-weight: 700;}
  .main-banner .swiper .swiper-slide:hover .img-box{width: 100%; height: 100%;}

  .main-banner .swiper .swiper-slide.large{width: 81.11vw;}
  .main-banner .swiper .swiper-slide.large .tit{max-height: 20.83vw; font-size: 5.56vw; font-weight: 700;}
  .main-banner .swiper .swiper-slide.large .desc{margin-top: 3.33vw; font-size: 3.89vw;}

  .main-banner .swiper .swiper-scrollbar{width: 68.89vw; height: 0.56vw; border-radius: 5.56vw; transition: 0s; }
  .main-banner .swiper .swiper-scrollbar:hover{height: 0.56vw;}

  .main-box-list{margin-left: -6.67vw; margin-right: -6.67vw; padding: 11.11vw 6.67vw;}
  .main-white-box.col > .txt-box{margin-bottom: 6.67vw;}

  .main-white-box + .main-white-box{margin-top: 16.67vw;}

  .main-white-box > .txt-box{margin-bottom: 6.67vw;}
  .main-white-box > .txt-box > .kind{margin-bottom: 3.89vw; font-size: 3.89vw; font-weight: 500;}
  .main-white-box > .txt-box > .kind::before{margin-right: 1.67vw; width: 6.67vw; height: 6.67vw;}
  .main-white-box > .txt-box > .tit{font-size: 5.56vw; }
  .main-white-box > .txt-box > .desc{margin-top: 2.50vw; font-size: 3.89vw;}
  .main-white-box > .btn-group{padding: 6.67vw 0 0;}

  .main-white-box .thumb-list{margin-left: -6.67vw; margin-right: -6.67vw; padding: 0 6.67vw;}

  .main-contents .thumb-list .swiper-slide.large .img-box{border-width: 0.28vw;}
  .main-contents .thumb-list .swiper-slide.large .tit{ max-height: 12.22vw; font-size: 4.44vw;}

  .main-contents .thumb-list .swiper-slide{margin-right: 4.44vw; width: 68.33vw;}
  .main-contents .thumb-list .swiper-slide:last-child{margin-right: 0;}

  .main-employ .thumb-list .swiper-slide{margin-right: 4.44vw; min-width: 56.67vw;}
  .main-employ .thumb-list .item .txt-box .tit{-webkit-line-clamp: 2;}

  .main-contents .thumb-list .swiper-slide .img-box{border-radius: 4.44vw; border-width: 0.28vw;}
  .main-contents .thumb-list .swiper-slide .sort{margin: 4.44vw 0 3.33vw; font-size: 3.89vw;}
  .main-contents .thumb-list .swiper-slide .sort strong::after{margin: 0 3.33vw; width: 0.28vw; height: 3.33vw;}
  .main-contents .thumb-list .swiper-slide .sort strong .icon{margin-right: 1.67vw; width: 5.56vw; height: 5.56vw;}
  .main-contents .thumb-list .swiper-slide .tit{max-height: 12.22vw; font-size: 4.44vw;}
  .main-contents .thumb-list .swiper-slide .txt{margin-top: 1.81vw; padding-top: 1.39vw; font-size: 2.22vw; border-top-width: 0.28vw;}


  .main-class .thumb-list .swiper-slide{margin-right: 4.44vw; min-width: 68.33vw;}
  .main-class .thumb-list .img-box{border-radius: 4.44vw; border-width: 0.28vw;}

  .main-class .thumb-list .txt-box .sort{margin: 4.44vw 0 3.33vw; font-size: 3.89vw;}
  .main-class .thumb-list .txt-box .sort strong{font-weight: 400;}
  .main-class .thumb-list .txt-box .sort strong::after{margin: 0 3.33vw; width: 0.28vw; height: 3.33vw;}
  .main-class .thumb-list .txt-box .tit{max-height: 12.22vw; font-size: 4.44vw; }
  .main-class .thumb-list .txt-box .hash-tag{margin-top: 1.67vw;}
  .main-class .thumb-list .txt-box .hash-tag span{margin-right: 2.78vw; font-size: 3.33vw;}

  .main-employ > .txt-box{width: 100%;}
  .main-employ .thumb-list .swiper-slide{display: block; margin-right: 4.44vw; min-width: 56.67vw; }

  .main-employ .thumb-list .item{border-width: 0.28vw; border-radius: 4.44vw;}
  .main-employ .thumb-list .item a{padding: 4.44vw;}
  .main-employ .thumb-list .item .badge{top: 4.44vw; right: 4.44vw;}
  .main-employ .thumb-list .item .img-logo{width: 13.33vw; height: 13.33vw; border-radius: 2.22vw; border-width: 0.28vw;}
  .main-employ .thumb-list .item .txt-box .sort{margin: 4.44vw 0 2.22vw; font-size: 3.89vw;}
  .main-employ .thumb-list .item .txt-box .sort strong::after{margin: 0 3.33vw; width: 0.28vw; height: 3.33vw;}
  .main-employ .thumb-list .item .txt-box .tit{font-size: 4.44vw;}
  .main-employ .thumb-list .item .txt-box .desc{margin-top: 4.44vw; padding-top: 3.33vw; font-size: 3.89vw; border-top-width: 0.28vw;}
  .main-employ .thumb-list .item .txt-box .desc span::after{margin: 0 3.33vw; width: 0.28vw; height: 3.33vw;}

  .main-partner{padding-top: 11.11vw;}
  .main-partner .banner{margin-bottom: 4.44vw; padding: 11.11vw 5.56vw 13.33vw; border-radius: 5.56vw; text-align: center;}
  .main-partner .banner .txt-box .tit{margin-bottom: 8.89vw; font-size: 5.56vw;}
  .main-partner .banner .btn-m{margin-top: 8.89vw;}

  .main-partner .banner.employ-service .img-box{position: initial; margin: 0 auto; width: 80%; transform: translateY(0);}
  .main-partner .banner.info-service::after{right: initial; left: 50%; transform: translate(-50%, -50%); width: 153.06vw; height: 113.89vw;}
  .main-partner .banner.info-service .img-box{position: relative; left: 50%; width: 36.94vw; box-shadow: 0 2.78vw 2.78vw rgba(0, 0, 0, 0.16); border-radius: 8.89vw; transform: translate(-50%, 0);}
  .main-partner .banner.info-service .btn-m{position: relative; z-index: 2;}

  .main-partner .partner-logo{grid-template-columns: repeat(2, 1fr); gap: 0 4.44vw; padding: 6.67vw; border-radius: 5.56vw;}
  .main-partner .partner-logo li{max-width: 34.44vw;}
  .main-partner .partner-logo li img{width: 80%;}
}


//fitness
.fitness{word-break: keep-all;}
.fitness section{padding: 140px 0;}

.fitness .notice-txt{@include flex(); width: fit-content; font-size: 24px; font-weight: 500;}
.fitness .notice-txt::before{content: ''; display: inline-block; margin-right: 5px; width: 24px; height: 24px; @include mask('common/ico_star.svg'); background-color: #D2DFF9;}
.fitness .notice-txt span{color: #D2DFF9;}

.fitness .sec-kv{position: relative; padding: 0;}
//.fitness .sec-kv{position: relative; @include bg('content/visual_fitness.jpg', $size: cover);}
.fitness .sec-kv .img-box{overflow: hidden; position: relative; height: 1000px; }
.fitness .sec-kv .img-box::before{content: ''; display: block; width: 100%; padding-top: 52%;}
.fitness .sec-kv .img-box img{position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); height: 100%; object-fit: cover;}
