.support .notice-detail .tit dd{font-size: 16px; line-height: 1.5; color: $c-gray-4;}

.support .notice-detail .con{padding: 32px 0; min-height: 528px; font-size: 16px; line-height: 1.5; color: $c-gray-4; border-top: 1px solid $c-gray-4; border-bottom: 1px solid $c-gray-6;;}
.support .notice-detail .con strong{display: inline-block; margin-bottom: 20px; font-size: 20px; line-height: 1.45; color: $c-gray-1;}

.support .btn-group .prev{margin-right: auto;}
.support .btn-group .next{margin-left: auto;}

