.program .program-banner {
	overflow: hidden;
}

.program .program-banner .swiper {
	position: relative;
	margin-bottom: 66px;
}

.program .program-banner .swiper-slide a {
	overflow: hidden;
	display: flex;
	justify-content: center;
	flex-direction: column;
	height: 170px;
	border-radius: 20px;
}

.program .program-banner .swiper-slide img {
	height: 100%;
	object-fit: cover;
}

.program .program-banner .txt-box {
	position: absolute;
	padding: 0 88px;
	width: 60%;
	word-break: keep-all;
}

.program .program-banner .txt-box .icon-img {
	height: 21px;
}

.program .program-banner .txt-box .icon-img img {
	width: auto;
	height: 100%;
}

.program .program-banner .txt-box .tit {
	margin-top: 8px;
	font-size: 28px;
	font-weight: 700;
	color: #141618;
}

.program .program-banner .swiper-pagination {
	bottom: -24px;
}

.program .program-banner .swiper-pagination-bullet {
	width: 6px;
	height: 6px;
	border-radius: 50%;
	background-color: #141618;
	opacity: 0.2;
}

.program .program-banner .swiper-pagination-bullet-active {
	width: 19px;
	border-radius: 3px;
	opacity: 1;
}

.thumb-box-list .tab-list .icon {
	background-color: #F4F5F8;
	border-radius: 50%;
}

.thumb-box-list .tab-list .icon-all {
	color: #000;
}

.thumb-box-list .tab-list .active .icon {
	background-color: #3575F6;
}

.thumb-box-list .icon {
	margin-right: 8px;
	flex-shrink: 0;
	width: 32px;
	height: 32px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: 20px auto;
}

.thumb-box-list .sort-select {
	margin-left: auto;
	flex-shrink: 0;
}

.thumb-box-list .thumb-list {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 24px;
	margin-top: 24px;
}

.thumb-box-list .thumb-list .item {
	overflow: hidden;
	border-radius: 20px;
}

.thumb-box-list .thumb-list .item:hover {
	box-shadow: 0 6px 18px rgba(0, 37, 106, 0.1);
}

.thumb-box-list .thumb-list .item a, .thumb-box-list .thumb-list .item button {
	display: block;
}

.thumb-box-list .thumb-list .item .txt-box {
	padding: 24px 24px 16px;
	background-color: #141618;
}

.thumb-box-list .thumb-list .item .img-box {
	position: relative;
	width: 100%;
	height: 260px;
}

.thumb-box-list .thumb-list .item .img-box img {
	height: 100%;
	object-fit: cover;
}

.thumb-box-list .thumb-list .item .img-box.video::after {
	content: "";
	display: block;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 60px;
	height: 60px;
	background: url("../img/common/ico_play.svg") no-repeat center/100% auto;
}

.thumb-box-list .thumb-list .item .img-box.video .play-time {
	position: absolute;
	bottom: 16px;
	right: 16px;
	padding: 4px 16px;
	width: fit-content;
	font-size: 14px;
	color: #fff;
	border-radius: 20px;
	background-color: #000;
}

.thumb-box-list.type2 .thumb-list .item {
	border: 1px solid #D8DDE2;
	transform: translateY(0);
	transition: transform 0.2s;
}

.thumb-box-list.type2 .thumb-list .item:hover {
	transform: translateY(-2.5%);
}

.thumb-box-list.type2 .thumb-list .item .txt-box {
	padding-bottom: 24px;
	background-color: #fff;
}

.thumb-box-list.type2 .thumb-list .item .tit {
	height: 60px;
	font-size: 20px;
	line-height: 1.4;
	color: #000;
}

.thumb-box-list.type2 .thumb-list .item .sort strong {
	color: #000;
}

.thumb-box-list .thumb-list .item .sort {
	display: flex;
	align-items: center;
	margin-bottom: 12px;
	font-size: 16px;
}

.thumb-box-list .thumb-list .item .sort strong {
	display: flex;
	align-items: center;
	font-weight: 500;
	color: #fff;
}

.thumb-box-list .thumb-list .item .sort strong::after {
	content: "";
	display: inline-block;
	margin: 0 12px;
	width: 1px;
	height: 12px;
	background-color: #D8DDE2;
}

.thumb-box-list .thumb-list .item .sort strong .icon {
	margin-right: 6px;
	width: 20px;
	height: 20px;
	background-color: transparent;
}

.thumb-box-list .thumb-list .item .sort span {
	color: #b9c0c6;
}

.thumb-box-list .thumb-list .item .tit {
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	height: 72px;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	font-size: 24px;
	font-weight: 500;
